import { createStore } from "redux";
import rootReducer from "./reducers";
import React from "react";
import { render } from "react-dom";
import { Provider } from "react-redux";
import App from "./components/App";
import { loadCarDataset } from "./utils";

window.Module = {
  onRuntimeInitialized: async function() {
    const points = await loadCarDataset(
      "./datasets/car.txt"
    );

    const attributes = [
      ["Type"],
      ["Power"],
      ["Transmission"],
      ["Price (USD)", { low: 1000, high: 50000, smallerBetter: true }],
      ["Year", { low: 2001, high: 2017, smallerBetter: false }],
      ["Power (HP)", { low: 50, high: 400, smallerBetter: false }],
      ["Used KM", { low: 10000, high: 150000, smallerBetter: true }]
    ];

    const mask = {};
    attributes.slice(0, 3).forEach(([attr]) => {mask[attr] = 1;});
    attributes.slice(3).forEach(([attr, config]) => {mask[attr] = 0;});

    const store = createStore(
      rootReducer,
      { points, attributes, mask }
      // window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__()
    );

    render(
      <Provider store={store}>
        <App />
      </Provider>,
      document.getElementById("root")
    );
  }
};

////// <script>  <body>  "web.js"  JavaScript  JavaScript 
const script = document.createElement("script");
script.src = "run.js";
script.async = true;
document.body.appendChild(script);
